<template>
  <div>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap fill-height>
        <v-flex xs12 >
          <v-layout justify-space-between>
            <v-flex xs7>
              <v-card-title>
                <h1>Розклад тижня</h1>
                <v-spacer/>
                <v-text-field
                  v-model="searchFilter"
                  append-icon="search"
                  label="Пошук"
                  class="search-field"/>
              </v-card-title>
            </v-flex>
            <v-flex xs4>
              <input
                type="file"
                placeholder="Імпорт розкладу"
                color="blue-grey"
                class="white--text"
                style="    width: 145px;"
                @click="loader = 'loadingFile'"
              >
              <v-btn
                :loading="loadingGenerate"
                :disabled="loadingGenerate"
                color="info"
                @click="loader = 'loadingGenerate'"
              >
                Згенерувати
                <span slot="loader" class="cu lstom-loader">
              <v-icon light>cached</v-icon>
              </span>
              </v-btn>
            </v-flex>
          </v-layout>

        </v-flex>
        <v-layout row>
          <v-flex xs7>
            <div class="data-table">
            <v-data-table
              :headers="headers"
              :items="tableData"
              :search="searchFilter"
              hide-actions
              disable-initial-sort
              class="elevation-1 data-table">
              <template slot="items" slot-scope="{ item }">
                <tr :active="item.Id === model.Id" @click="rowSelected(item)">
                  <td class="text-xs-left">{{ item.GroupName }}</td>
                  <td class="text-xs-left">{{ item.SpecialtyName }}</td>
                  <td class="text-xs-left">{{ item.SubjectName }}</td>
                  <td class="text-xs-left">{{ item.TeacherName }}</td>
                  <td class="text-xs-left">{{ item.AuditoryName }}</td>
                  <td class="text-xs-left">{{ item.DayName }}</td>
                </tr>
              </template>
            </v-data-table>
            </div>
            <v-layout align-center justify-space-between row mt-3>
              <div>
                <v-btn @click="addEvent" v-show="!isChanged" color="success">Додати</v-btn>
                <v-btn @click="deleteEvent" v-show="!isChanged" color="error">Видалити</v-btn>
              </div>
              <div>
                <v-btn @click="cancelEvent" v-show="isChanged" color="normal">Скасувати</v-btn>
                <v-btn @click="saveEvent" v-show="isChanged" color="info">Зберегти</v-btn>
              </div>
            </v-layout>
          </v-flex>
          <v-flex xs-6 offset-xs1>
            <v-form ref="form" v-model="valid" lazy-validation>

              <v-select
                :items="groupKeyValues"
                v-model="model.GroupId"
                :rules="rules.GroupId"
                label="Група"
                required
                validate-on-blur
              />
              <v-select
                :items="subjectKeyValues"
                v-model="model.SubjectId"
                :rules="rules.SubjectId"
                label="Предмет"
                required
                validate-on-blur
              />
              <v-select
                :items="teacherKeyValues"
                v-model="model.TeacherId"
                :rules="rules.TeacherId"
                label="Викладач"
                required
                validate-on-blur
              />
              <v-select
                :items="auditoryKeyValues"
                v-model="model.AuditoryId"
                :rules="rules.AuditoryId"
                label="Аудиторія"
                required
                validate-on-blur
              />
              <v-select
                :items="scheduleTypeKeyValues"
                v-model="model.ScheduleTypeId"
                :rules="rules.ScheduleTypeId"
                label="Тип"
                required
                validate-on-blur
              />
              <v-select
                :items="weekDaysKeyValues"
                v-model="model.Day"
                :rules="rules.Day"
                label="День"
                required
                validate-on-blur
              />
              <v-select
                :items="lessonNumberKeyValues"
                :rules="rules.LessonNumber"
                v-model="model.LessonNumber"
                label="Номер пари"
                required
                validate-on-blur
              />
            </v-form>
          </v-flex>
        </v-layout>
      </v-layout>
    </v-container>
    <confirm-dialog
      ref="confirm"
      title="Ви впевнені?"
      text="Відмінити видалення неможливо."
    />
  </div>
</template>

<script>
import './store'
import mixin from '../../mixin'
import { mapActions, mapGetters } from 'vuex'
import ConfirmDialog from '../../shared/ConfirmDialog'

export default {
  mixins: [
    mixin
  ],
  components: {
    ConfirmDialog
  },
  constants: {
    headers: [
      { text: 'Група', value: 'GroupName' },
      { text: 'Спеціальність', value: 'SpecialtyName' },
      { text: 'Предмет', value: 'SubjectName' },
      { text: 'Викладач', value: 'TeacherName' },
      { text: 'Аудиторія', value: 'AuditoryName' },
      { text: 'День', value: 'DayName' }
    ],
    rules: {
      AuditoryId: [
        v => !!v || 'Поле аудиторія обов\'язкове'
      ],
      GroupId: [
        v => !!v || 'Поле група обов\'язкове'
      ],
      TeacherId: [
        v => !!v || 'Поле викладач обов\'язкове'
      ],
      SubjectId: [
        v => !!v || 'Поле предмет обов\'язкове'
      ],
      ScheduleTypeId: [
        v => !!v || 'Поле тип предмету обов\'язкове'
      ],
      LessonNumber: [
        v => !!v || 'Поле номер пари обов\'язкове'
      ],
      Day: [
        v => !!v || 'Поле день обов\'язкове'
      ]
    }
  },
  data () {
    return {
      loader: null,
      loadingFile: false,
      loadingGenerate: false
    }
  },
  computed: {
    ...mapGetters('admin/weekschedule', {
      tableData: 'weekScheduleList',
      auditoryKeyValues: 'auditoryKeyValues',
      groupKeyValues: 'groupKeyValues',
      subjectKeyValues: 'subjectKeyValues',
      teacherKeyValues: 'teacherKeyValues',
      scheduleTypeKeyValues: 'scheduleTypeKeyValues',
      weekDaysKeyValues: 'weekDaysKeyValues',
      lessonNumberKeyValues: 'lessonNumberKeyValues'
    }),
    modelsEqual () {
      return (
        global.valueEquals(this.model.AuditoryId, this.modelCopy.AuditoryId) &&
        global.valueEquals(this.model.SubjectId, this.modelCopy.SubjectId) &&
        global.valueEquals(this.model.TeacherId, this.modelCopy.TeacherId) &&
        global.valueEquals(this.model.Day, this.modelCopy.Day) &&
        global.valueEquals(this.model.GroupId, this.modelCopy.GroupId)
      )
    }
  },
  methods: {
    ...mapActions('admin/weekschedule', [
      'getAll',
      'getById',
      'getAuditories',
      'getGroups',
      'getSubjects',
      'getTeachers',
      'getScheduleTypes',
      'add',
      'update',
      'delete'
    ])
  },
  watch: {
    loader () {
      const l = this.loader
      this[l] = !this[l]
      setTimeout(() => (this[l] = false), 3000)
      this.loader = null
    }
  },
  created () {
    this.getAuditories()
    this.getGroups()
    this.getSubjects()
    this.getTeachers()
    this.getScheduleTypes()
  }
}
</script>

<style scoped>
  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>



